.chat-threads__list-item {
  transition: background-color 200ms, border 200ms;
  cursor: pointer;
  border-left: 3px solid transparent;
  border-radius: 10px;
  padding: 0.5em;

  &:hover {
    background-color: #293038;
  }

  &.active-thread {
    border-left: 3px solid #7abcff;
    background-color: #444c57;
  }
}
